import { GET_POSTS, GET_FILTERED_POSTS } from '../actions';

const INITIAL_STATE = {
  posts: [],
  filteredPosts: []
}

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case GET_POSTS:
      return {...state, posts: action.payload};
    case GET_FILTERED_POSTS:
      return {...state, filteredPosts: action.payload}
    default:
      return state;
  }
}